//* __________________   "performance" ______________________
export function tlVerticalOpacity() {
	gsap.to('.el-4', {
		opacity: 0, //  
		ease: 'none', //    
		scrollTrigger: {
			trigger: '.performance',
			start: 'top top', //  ,  `.performance`  80%  
			//   
			end: 'bottom top', //  ,  `.performance`  
			// 
			scrub: 2, //       
			// markers: true, //   (  )
		}
	});
}

