//* _________________________ : Parallax _______________________________
export function cassieEvans() {
	smoother.effects('.parallax__image-cont', {
		speed: () => gsap.utils.random(0.55, 0.85, 0.05)
	});

	gsap.to('.parallax__anime-swipe', {
		yPercent: 300,
		delay: 0.2,
		duration: 3,
		stagger: {
			from: 'random',
			each: 0.1
		},
		ease: 'sine.out'
	});

	gsap.to('.parallax__image-cont img', {
		scale: 1.5,
		xPercent: 20,
		scrollTrigger: {
			trigger: '.parallax',
			start: 'top top',
			end: '+=3000px',
			scrub: true
		}
	});
}

